
/* start */
@keyframes floatBubble {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-10px); }
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

@keyframes scaleIn {
  from { transform: scale(0.9); opacity: 0; }
  to { transform: scale(1); opacity: 1; }
}

@keyframes slideInLeft {
  from { transform: translateX(-50px); opacity: 0; }
  to { transform: translateX(0); opacity: 1; }
}

@keyframes slideInRight {
  from { transform: translateX(50px); opacity: 0; }
  to { transform: translateX(0); opacity: 1; }
}

@keyframes softGlow {
  0% { box-shadow: 0 0 5px rgba(217, 103, 4, 0.2); }
  50% { box-shadow: 0 0 20px rgba(217, 103, 4, 0.4); }
  100% { box-shadow: 0 0 5px rgba(217, 103, 4, 0.2); }
}

.StartContainer {
  width: 100%;
  height: auto;
  margin: 0 auto;
  position: relative;
  opacity: 0;
  animation: fadeIn 1s ease-out forwards;
}
.content-top {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  position: relative;
  padding: 80px 5% 0;
}
.main-image-container {
  -webkit-user-select: none;  
  -moz-user-select: none;  
  -ms-user-select: none; 
  width: 100%;
  max-width: 1100px;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  opacity: 0;
  animation: scaleIn 1s ease-out 0.5s forwards;
}
.main-image {
  width: 100%;
  height: auto;
  object-fit: cover;
  transition: transform 0.5s ease;
}
.top-left-text,.bottom-right-text{
  position: absolute;
  color: #ffffff;
  font-weight: 900;
  opacity: 0;
}
.top-left-text {
  line-height: 1.36;
  top: 251px;
  left: 8.2%;
  font-size: 5.8vw;
  animation: slideInLeft 1s ease-out 1s forwards;
}
.bottom-right-text {
  letter-spacing: -1px;
  line-height: 1.3;
  text-align: right;
  bottom: 27.8%;
  right: 8.6%;
  font-size: 6vw;
  animation: slideInRight 1s ease-out 1.5s forwards;
}
.bottom-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  opacity: 0;
  animation: fadeIn 1s ease-out 2s forwards;
}
.bottom-text {
  text-align: right;
  font-size: 2.5vw;
  margin-top: 25px;
  margin-bottom: 60px;
  color: #395873;
  background: #ffffff63;
  padding: 20px;
  border-radius: 5px;
  transition: all 0.3s ease;
  position: relative;
}
.bottom-text:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px rgba(0,0,0,0.1);
  background: #ffffffaa;
  animation: softGlow 2s infinite;
}
.cta-button {
  background-color: #D96704;
  color: #F2E8DF;
  padding: 15px 6%;
  border: none;
  border-radius: 4px;
  font-size: 0.6em;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.3s ease, box-shadow 0.3s ease;
  position: absolute;
  right: 0;
  bottom: -65px;
}
.cta-button:hover {
  background-color: #F2AE72;
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(0,0,0,0.2);
  background-image: url('./Assets/Img/CARE.png');
}

.bubbles-container {
  position: absolute;
  width: 100%;
  height: 300px;
  margin-top: 50px;
  z-index: 1000;
}

.blueBubble, .whiteBubble, .bottom-static-bubble {
  position: absolute;
  border-radius: 50%;
  opacity: 0;
  transform: scale(0); 
  transition: all 0.5s ease-out;
  user-select: none;  
  -webkit-user-select: none;  
  -moz-user-select: none;  
  -ms-user-select: none; 
}

.blueBubble {
  background-color: #C3EEF4;
}
.whiteBubble{
  background-color: #ffffff; 
}

.bottom-static-bubble {
  transition: transform 0.5s ease-out, opacity 0.5s ease-out;
}

