/*Revievs*/
.review-testimonials-section {
  position: relative;
  width: 100%;
  display: flex;
  text-align: center;
  background-color: #395873;
  color: #F2E8DF;
  height: 1035px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.review-intro-text, .review-outro-text {
  color: #F2E8DF;
  background-color: #f2ae72cc;
  border-radius: 10px;
  position: absolute;
  max-width: 90%;
  box-sizing: border-box;
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 0.5s ease, transform 0.5s ease;
}

.review-intro-text.visible, .review-outro-text.visible {
  opacity: 1;
  transform: translateY(0);
}

.review-intro-text {
  top: 0;
  width: 57.5%;
  height: auto;
  font-size: 43px;
  padding: 20px;
}

.review-outro-text {
  bottom: 0;
  width: 47.2%;
  height: auto;
  font-size: 39.1px;
  padding: 20px;
}

.review-carousel-container {
  position: relative;
  overflow: hidden;
  padding: 25px 0;
  margin-top: 125px;
  width: 100%;
}

.review-carousel {
  display: flex;
  transition: transform 0.5s cubic-bezier(0.25, 0.1, 0.25, 1);
  cursor: grab;
  user-select: none;
}

.review-carousel:active {
  cursor: grabbing;
}

.review-testimonial-card {
  flex: 0 0 calc(25% - 20px);
  min-width: calc(25% - 20px);
  height: 420px;
  margin: 0 10px;
  background-color: #F2E8DF;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(38, 17, 13, 0.2);
  padding: 10px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.review-testimonial-card:hover {
  transform: rotate(0) scale(1.05) !important;
  box-shadow: 0 8px 12px rgba(38, 17, 13, 0.3);
}

.review-testimonial-image {
  width: 100%;
  height: 80%;
  object-fit: cover;
  border-radius: 8px 8px 0 0;
}

.review-testimonial-content {
  font-style: italic;
  color: #26110D;
  margin-top: 10px;
  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  transition: transform 0.3s ease, color 0.3s ease;
}

.review-testimonial-card:hover .review-testimonial-content {
  transform: scale(1.05);
  color: #395873;
}

/*questionsBlock*/

.questions-QuestionBlock {
  width: 100%;
  height: 100vh;
  position: relative;
  overflow: hidden;
  background-color: #395873;
}

.questions-container {
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 90%;
  max-width: 1200px;
}

.questions-container h1 {
  font-size: 8vw;
  color: #F2E8DF;
  margin: 0;
  line-height: 1.2;
  opacity: 0;
  transform: translateY(-20px);
}

.questions-container p {
  font-size: 3vw;
  color: #F2AE72;
  margin: 1rem 0;
  opacity: 0;
  transform: translateY(-20px);
}

.questions-phone-number {
  position: relative;
  display: inline-block;
  cursor: pointer;
}

.questions-phone-number::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: #F2AE72;
  transform: scaleX(0);
  transform-origin: bottom right;
  transition: transform 0.3s ease-out;
}

.questions-phone-number:hover::after {
  transform: scaleX(1);
  transform-origin: bottom left;
}

.questions-book-now {
  background-color: #D96704;
  color: #F2E8DF;
  padding: 2vw 4vw;
  border: none;
  border-radius: 4vw;
  font-size: 2.5vw;
  cursor: pointer;
  transition: all 0.3s ease;
  opacity: 0;
  transform: scale(0.9);
  position: relative;
  overflow: hidden;
}

.questions-book-now:hover {
  background-color: #F2AE72;
  color: #26110D;
  transform: scale(1.05);
  box-shadow: 0 5px 15px rgba(0,0,0,0.2);
  font-size: 2.3vw;
  padding: 1.9vw 3.8vw;
}

.questions-book-now::before,
.questions-book-now::after {
  content: "•";
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  font-size: 4vw;
  transition: all 0.3s ease;
}

.questions-book-now::before {
  right: 2vw;
}

.questions-book-now::after {
  left: -3vw;
}

.questions-book-now:hover::before {
  right: -3vw;
}

.questions-book-now:hover::after {
  left: 2vw;
}

.questions-tennis-ball {
  position: absolute;
  width: 60px;
  height: 60px;
  background-color: #F2AE72;
  border-radius: 50%;
  opacity: 0;
  transition: opacity 1s ease;
}

@keyframes questions-fadeInUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes questions-fadeInScale {
  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes questions-fadeIn {
  to {
    opacity: 0.8;
  }
}
@media (max-width: 1200px) {
  .review-testimonial-card {
      flex: 0 0 calc(33.33% - 20px);
      min-width: calc(33.33% - 20px);
  }
  .review-intro-text, .review-outro-text {
      font-size: 36px;
  }
}

@media (max-width: 1024px) {
  .top-left-text, .bottom-right-text {
      font-size: 4vw;
  }
  .bottom-text, .AnimalsTopText {
      font-size: 3vw;
  }
  .questions-container h1 {
      font-size: 10vw;
  }
  .questions-container p {
      font-size: 4vw;
  }
  .questions-book-now {
      font-size: 3.5vw;
      padding: 2.5vw 5vw;
  }
  .questions-book-now:hover {
      font-size: 3.4vw;
      padding: 2.4vw 4.8vw;
  }
}

@media (max-width: 900px) {
  .review-testimonial-card {
      flex: 0 0 calc(50% - 20px);
      min-width: calc(50% - 20px);
  }
  .review-intro-text, .review-outro-text {
      font-size: 32px;
  }
}

@media (max-width: 768px) {
  .top-left-text, .bottom-right-text {
      position: static;
      text-align: center;
      margin: 20px 0;
  }
  .bottom-text, .AnimalsTopText {
      font-size: 4vw;
  }
  .questions-container h1 {
      font-size: 12vw;
  }
  .questions-container p {
      font-size: 5vw;
  }
  .questions-book-now {
      font-size: 4.5vw;
      padding: 3vw 6vw;
  }
  .questions-book-now:hover {
      font-size: 4.4vw;
      padding: 2.9vw 5.8vw;
  }
  .bottom-bubble, .middle-bubble {
    width: calc(var(--bubble-width) * 1.2) !important;
    height: calc(var(--bubble-width) * 1.2) !important;
  }
}

@media (max-width: 600px) {
  .review-testimonial-card {
      flex: 0 0 calc(100% - 20px);
      min-width: calc(100% - 20px);
  }
  .review-intro-text, .review-outro-text {
      font-size: 24px;
  }
}

@media (max-width: 480px) {
  .top-left-text, .bottom-right-text {
      font-size: 6vw;
  }
  .bottom-text, .AnimalsTopText {
      font-size: 5vw;
  }
  .questions-container h1 {
      font-size: 14vw;
  }
  .questions-container p {
      font-size: 6vw;
  }
  .questions-book-now {
      font-size: 5.5vw;
      padding: 3.5vw 7vw;
  }
  .questions-book-now:hover {
      font-size: 5.4vw;
      padding: 3.4vw 6.8vw;
  }
  .questions-book-now::before,
  .questions-book-now::after {
      font-size: 6vw;
  }
  .bottom-bubble, .middle-bubble {
    width: calc(var(--bubble-width) * 1.35) !important;
    height: calc(var(--bubble-width) * 1.35) !important;
  }
}

