.auth-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(38, 17, 12, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.auth-modal {
  background-color: #FFFFFF;
  padding: 2.5rem;
  border-radius: 23px;
  box-shadow: 0 4px 6px rgba(38, 17, 12, 0.1);
  width: 26.3%;
  max-width: 400px;
  min-height: 470px;
  position: relative;
  animation: auth-modalFadeIn 0.3s ease-out;
  overflow: hidden;
}

@keyframes auth-modalFadeIn {
  from { opacity: 0; transform: translateY(-20px); }
  to { opacity: 1; transform: translateY(0); }
}

.auth-close-button {
  position: absolute;
  top: 0px;
  right: 1%;
  font-size: 2.2rem;
  background: none;
  border: none;
  cursor: pointer;
  color: #7F7F7F;
  transition: all 0.3s ease;
}

.auth-close-button:hover {
  color: #F2AE72;
  transform: rotate(90deg);
}

.auth-tabs {
  display: flex;
  top: 8px;
  left: 10.2%;
  position: relative;
  border-bottom: 3px solid #000000;
  width: 79.8%;
}

.auth-tab {
  flex: 1 1;
  background: none;
  border: none;
  font-size: 1.7vw;
  cursor: pointer;
  color: #7F7F7F;
  transition: color 0.3s ease, transform 0.3s ease;
}

.auth-tab:hover {
  transform: translateY(-2px);
}

.auth-tab.active {
  color: #26110C;
  font-weight: 500;
}

.auth-tabs::after {
  content: '';
  position: absolute;
  bottom: -3px;
  left: 0;
  width: 50%;
  height: 3px;
  background-color: #FF7700;
  transition: transform 0.3s ease;
}

.auth-tabs:has(.auth-tab:nth-child(2).active)::after {
  transform: translateX(100%);
}

.auth-form-container {
  display: flex;
  transition: transform 0.3s ease;
  width: 182%;
  margin-left: 4.5%;
}

.auth-form-container.signup {
  transform: translateX(0);
}

.auth-form-container.signin {
  transform: translateX(-50%);
}

.auth-form {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  width: 91%;
  margin-top: 68px;
  flex: 1 1;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.auth-form-container.signup .auth-form-signup,
.auth-form-container.signin .auth-form-signin {
  opacity: 1;
}

.auth-form-group {
  margin-bottom: 0.9rem;
}

.auth-form-group label {
  line-height: 1.4;
  margin-left: 4.41%;
  font-size: 0.94vw;
  display: block;
  color: #000000;
  font-weight: 500;
  transition: color 0.3s ease;
}

.auth-form-group:hover label {
  color: #FF7700;
}

.auth-form-group input {
  width: 91%;
  padding: 0.985vw;
  border: 1px solid #7F7F7F;
  border-radius: 16px;
  font-size: 0.94rem;
  transition: border-color 0.3s ease, box-shadow 0.3s ease;
}

.auth-form-group input::placeholder {
  color: #7F7F7F;
}

.auth-form-group input:focus {
  outline: none;
  border-color: #FF7700;
  box-shadow: 0 0 0 2px rgba(255, 119, 0, 0.2);
}

.auth-form-group input.auth-error {
  border-color: #FF7700;
}

.auth-error-message {
  color: #FF7700;
  font-size: 0.875rem;
  margin-top: 0.5rem;
  margin-bottom: 0;
  animation: auth-errorFadeIn 0.3s ease-out;
}

@keyframes auth-errorFadeIn {
  from { opacity: 0; transform: translateY(-5px); }
  to { opacity: 1; transform: translateY(0); }
}

.auth-password-input {
  position: relative;
}

.auth-password-toggle {
  position: absolute;
  right: 1.9%;
  top: 55%;
  transform: translateY(-50%);
  background: none;
  border: none;
  cursor: pointer;
  color: #CCCCCC;
  transition: color 0.3s ease, transform 0.3s ease;
}

.auth-password-toggle:hover {
  color: #FF7700;
  transform: translateY(-50%) scale(1.1);
}

.auth-password-hint {
  font-size: 0.927vw;
  color: #000000;
  margin: 0;
  margin-left: 4.3%;
  margin-bottom: 17px;
  transition: color 0.3s ease;
}

.auth-password-hint:hover {
  color: #FF7700;
}

.auth-forgot-password {
  font-size: 1.054vw;
  margin-left: 3.9%;
  align-self: flex-start;
  background: none;
  border: none;
  color: #FF7700;
  font-weight: medium;
  cursor: pointer;
  margin-bottom: 0.9rem;
  padding: 0;
  position: relative;
  transition: color 0.3s ease;
}

.auth-forgot-password::after {
  content: '';
  position: absolute;
  width: 0;
  height: 2px;
  bottom: -2px;
  left: 0;
  background-color: #FF7700;
  transition: width 0.3s ease;
}

.auth-forgot-password:hover {
  color: #e56a00;
}

.auth-forgot-password:hover::after {
  width: 100%;
}

.auth-submit-button {
  background-color: #FF7700;
  color: white;
  padding: 1rem;
  border: none;
  border-radius: 25px;
  font-size: 1.05vw;
  cursor: pointer;
  font-weight: 500;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.auth-submit-button:hover {
  background-color: #e56a00;
  transform: translateY(-2px);
}

.auth-submit-button:active {
  transform: scale(0.98);
}

@media (max-width: 1024px) {
  .auth-modal {
    width: 40%;
    max-width: none;
  }
}

@media (max-width: 768px) {
  .auth-modal {
    width: 60%;
    height: auto;
    min-height: 401px;
  }
}

@media (max-width: 480px) {
  .auth-modal {
    padding: 2rem;
    width: 95%;
  }
  
  .auth-tab {
    font-size: 1rem;
    padding: 0.75rem;
  }
  
  .auth-form-group input {
    padding: 0.875rem;
  }
  
  .auth-submit-button {
    padding: 0.875rem;
    font-size: 1rem;
  }
}

@media (max-width: 380px) {
  .auth-modal {
    padding: 1.5rem;
  }
  
  .auth-tabs {
    left: 5%;
    width: 90%;
  }
  
  .auth-form-group input {
    width: 100%;
    padding: 0.75rem;
  }
  
  .auth-submit-button {
    padding: 0.75rem;
    font-size: 0.9rem;
  }
}